@import "variables.scss";

.about {
  width: 100%;
  padding: 1rem;
  
  background-color:#f7f7f7;
  background-size: cover;
  background-repeat: no-repeat;

  box-shadow: 2px 0 13px rgba(#000, .2);

  border: none;

  text-align: center;
}

.heading {
  color: $color-primary;
  font-size: 6rem;
  font-weight: 300;
  
  margin-top: 2rem;
  margin-bottom: 4rem;
}

.cardContainer {
  margin: 2rem 0;
}