@import "variables.scss";

.card {
  background-color: #fff;
  text-align: left;

  margin: 1rem 2rem;
  padding: 3rem 2rem;
  flex: 1 1 0;

  border-radius: 5px;
  box-shadow: 5px 0 10px rgba($color-grey-dark-2, .4);
}

.heading {
  font-size: 2.3rem;
  font-weight: 400;
  color: $color-grey-dark-2;

  display: inline-block;
  width: 90%;
  text-align: center;

  //padding: 1rem 0;
  border-bottom: 3px solid $color-primary-light;
  margin-bottom: 2rem;

  padding: .2rem 0;
}

.infoContainer {
  margin: 0 auto;
  width: 90%;
}

@media (max-width: 1000px) {
  .heading {
    font-size: 1.9rem;
  }
}

@media (max-width: 840px) {
  .card {
    text-align: center;
  }

  .infoContainer {
    width: 70%;
  }
}