@import "variables.scss";

.nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  width: 100%;
  position: fixed;
  padding: .5rem 0;

  //background-color: $color-grey-dark-2;
  background-color: rgba($color-primary-dark, .95);

  z-index: 100;
}

.linkContainer {
  list-style: none;
  display: flex;
  flex-direction: row;

  justify-content: center;
  align-items: center;

  flex: 0 0 60%;
}

.toggle {
  display: none;
}

@media (max-width:750px) {
  .linkContainer {
    flex: 0 0 50%;
  }
}

@media (max-width: 520px) {
  .nav {
    height: 100%;
    width: 60%;

    flex-direction: column-reverse;
    justify-content: space-between;

    transition: all .2s ease-in;
    
  }

  .toggle {
    display: block;
    position: fixed;
    top: 1rem;
    left: 1rem;

    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    z-index: 200;

    background: transparent;
    border: 0;

  }

  .toggleIcon {
    width: 4rem;
    height: 4rem;
    fill: white;
  }

  .collapsed {
    width: 0;
    opacity: 0;

    .toggle {
      opacity: 1;
    }
  }

  .linkContainer {
    flex-direction: column;
    justify-content: flex-start;

    display: block;
    text-align: center;

    width: 100%;
    margin-top: 4rem;
  }
}