@import "variables.scss";

.achievements {
  width: 100%;
  padding: 1rem 2rem;

  background-color: #f7f7f7;

  text-align: center;
}

.description {
  margin-top: 2rem;
}

.header {
  border-bottom: 2px solid $color-grey;
  width: 60%;
  margin: 0 auto;
  margin-bottom: .7;
}

.title {
  font-size: 4.5rem;
  color: $color-grey-dark-2;
  font-weight: 300;
}

.date {
  font-size: 2.4rem;
  color: $color-grey-dark;
  font-weight: 300;
}

.places {
  display: flex;
  flex-direction: row;

  justify-content: center;

  width: 40%;
  margin: 0 auto;
  margin-top: .5rem;
}

@media (max-width: 1000px) {
  .places {
    width: 60%;
  }
}

@media (max-width: 800px) {
  .places {
    width: 70%;
  }
}

@media (max-width: 600px) {
  .places {
    flex-direction: column;
    align-items: center;
  }

  .title {
    font-size: 4.0rem;
  }

  .date {
    font-size: 1.9rem;
  }
}

@media (max-width: 450px) {
  .header {
    width: 80%;
  }
}