@import "variables";

.link {
  display: inline-block;
  height: 100%;
  
  padding: .3rem .5rem;
  margin: 0 .2rem;

  border-radius: 5px;

  text-decoration: none;
  font-size: 2rem;
  color: white;
  outline: none;

  &:hover,
  &.active {
    background-color: $color-primary; 
  }
}

@media (max-width: 600px) {
  .link {
    font-size: 1.7rem;
  }
}

@media (max-width: 520px) {
  .link {
    flex: 1 1 0;
    height: 3rem;
    width: 95%;
    font-size: 2rem;
    border-radius: 0;

    border-bottom: 1px solid #fff;

    transition: all .2s ease-in;
  }
}