@import "variables";

.place {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  flex: 1 1 0;
}

.icon {
  $size: 3.7rem;
  height: $size;
  flex: 0 0 $size;
}

.name {
  font-size: 2rem;
  text-align: left;
}

.first {
  fill: #E0BF00;
}

.second {
  fill: $color-grey;
}

.third{
  fill: #CD7F32;
}

@media (max-width: 600px) {
  .place {
    width: 50%;
    justify-content: start;
  }

  .icon {
    height: 3.3rem;
  }
}