@import "_variables.scss";

.header {
  width: 100%;
  height: 100vh;

  display: block;

  background-image: 
    linear-gradient( to right bottom, rgba($color-primary-light, .8), rgba($color-primary-dark, .8) ),
    url(./hero.jpg);
  
  background-size: cover;
  text-align: center;

  margin-bottom: 1px solid $color-grey-dark-3;
}

.textContainer {
  position: relative;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

$easing: cubic-bezier(.51,.31,.21,1);

.headingSecondary {
  font-size: 6rem;
  color: $color-grey-light;
  font-weight: 400;

  animation: move-in-left .5s $easing;
}

.headingPrimary {
  font-size: 7rem;
  color: $color-grey-light;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: .2rem;

  animation: move-in-right .5s $easing;
}

@keyframes move-in-left {
  from {
    transform: translateX(-10rem);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes move-in-right {
  from {
    transform: translateX(10rem);
  }

  to {
    transform: translateX(0);
  }
}

@media (max-width: 630px) {
  .headingSecondary {
    font-size: 5rem;
  }

  .headingPrimary {
    font-size: 6rem;
  }
}

@media (max-width: 460px) {
  .headingSecondary {
    font-size: 4rem;
  }

  .headingPrimary {
    font-size: 5rem;
  }
}