@import "variables.scss";

.info {
  display: flex;
  justify-content: flex-start;

  width: 100%;
  margin-bottom: .7rem;

  padding: .3rem 0;
  border-bottom: 1px solid $color-grey-dark;
}

.icon {
  width: 2rem;
  height: 2rem;
  fill: $color-primary-dark;

  display: inline-block;
  margin-right: 1rem;
}

.text {
  font-size: 1.8rem;
  font-weight: 400;
  text-transform: capitalize;
  text-align: left;
}

@media (max-width: 1000px) {
  .text {
    font-size: 1.6rem;
  }
}