@import "variables.scss";

.locations {
  width: 100%;
  background-color: $color-primary;
  text-align: center;
  padding: 1rem 0 2rem 0;
}

.cardContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  width: 90%;
  margin: 0 auto;
}

@media (max-width: 840px) {
  .cardContainer {
    flex-direction: column;
    width: 70%;
  }
}

@media (max-width: 550px) {
  .cardContainer {
    width: 85%;
  }
}