.container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  flex: 0 0 20%;

  padding: 0 1.5rem;

  color: white;
}

.phoneInfo {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.name {
  font-size: 1.5rem;
  margin-left: .4rem;
}

.icon {
  width: 1.5rem;
  height: 1.5rem;

  margin-right: .4rem;

  fill: white;
}

.phone {
  font-size: 1.2rem;
  text-decoration: none;
  color: inherit;
}

@media (max-width: 750px) {
  .container {
    flex: 0 0 30%;
  }
}

@media (max-width: 600px) {
  .name {
    font-size: 1.2rem;
  }

  .icon {
    width: 1.2rem;
    height: 1.2rem;
  }

  .phone {
    font-size: 1.0rem;
  }
}

@media (max-width: 520px) {
  .container {
    justify-content: flex-end;
    width: 100%;

    & > * {
      transition: all .1s .1s ease-in;
    }
  }

  .name {
    font-size: 1.6rem;
    padding-top: .3rem;
    border-top: 1px solid #fff;

    width: 100%;
    text-align: center;
  }

  .phoneInfo {
    justify-content: center;
  }

  .icon {
    width: 1.6rem;
    height: 1.6rem;
  }

  .phone {
    font-size: 1.3rem;
  }
}